@mixin btn-anim($fill: false) {
	transition: all .2s linear;

  &:focus{
		outline: none;
	}

	&:focus-visible {
    @if $fill {
      background-color: #bc572b;
    } @else {
      background-color: #ff6e30;
    }
	}

	&:hover {
    @if $fill {
      background-color: #ff6e30;
      border: 3px solid #ce4307;
    } @else {
      background-color: #bc572b;
      border: 3px solid #ff6e30;
    }
	}

	&:active {
    background-color: #ce4307;
	}
}
