.header {
  background-color: red;

  &::before {
    @include css-triangle(blue, down, 200px, relative);
  }
}

h1 {
  font-family: 'TTFirsNeue';
  font-weight: 400;
  line-height: 21px;
  color: #aaa;
}

h2 {
  font-family: 'TTFirsNeue';
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #FF6E30;

}

h3 {
  font-family: 'TTFirsNeue';
  font-weight: 700;
  font-size: 24px;
  line-height: 104%;
  color: #202020;
}

.p1 {
  position: relative;
  font-family: 'Roboto';
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #CACACA;
  &::before {
    @include css-triangle(red, down, 50px);
    left: 20px;
    top: 10px;
  }
}

.p2 {
  @include font-firsNeue(900, 20px, 20px, #aa0f0f);
  @include mq(1024px) {
    font-size: 116px;
  }
  @include bg('../img/img.jpg');
  @include flex-all-sb;
}

input {
  @include input-placeholder {
    color: #FF6E30;
    font-size: 40px;
  }
}

.btn {
  @include btn_anim;
}

.divwrap {
  width: 200px;
  height: 200px;
  background-color: #aaa;
  position: relative;
}

.div {
  @include align(1, 1, absolute);
  width: 50px;
  height: 50px;
  background-color: red;
}
